<template>
  <ul class="message_detail">
    <li>{{$route.params.id}}</li>
    <li>{{message.title}}</li>
    <li>{{message.content}}</li>
  </ul>
</template>

<script>
  const messages = [
    {id:1, title:"Time is running!", content: "Êó∂Èó¥Á¥ßËø´ÂïäÔºÅ"},
    {id:3, title:"You gota working harder!", content: "Êõ¥Âä™ÂäõÊâçË°åÔºÅ"},
    {id:5, title:"Life is hard.", content: "ÁîüÊ¥ª‰∏çÊòìÔºÅ"}
  ];
  export default {
    name: 'Header',
    data () {
      return {
        message: {}
      }
    },
    mounted () {
      const id = this.$route.params.id * 1;
      
      // Êü•ÊâæÂØπÂ∫îÁöÑdetail
      this.message = messages.find(detail => detail.id===id)
    },
    watch: {
      $route (value) { // valueÊòØ$routeÁöÑÊúÄÊñ∞ÂÄº
        const id = value.params.id * 1;
        
        // Êü•ÊâæÂØπÂ∫îÁöÑdetail
        this.detail = messages.find(detail => detail.id===id);
      }
    }
  }
</script>

<style scoped>
  /* Header */
  
</style>



